html, body {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center; }
  html .box, body .box {
    width: 300px;
    height: 300px;
    background: linear-gradient(-45deg, white 0%, white 25%, hotpink 25%, hotpink 50%, white 50%, white 75%, hotpink 75%, hotpink 100%);
    background-size: 10%;
    box-sizing: border-box;
    padding: 15px;
    box-shadow: 0 0 2px deeppink, 0 0 5px black, inset 0 0 5px black;
    border-radius: 10px;
    animation: animate 2s linear infinite; }
    html .box .content, body .box .content {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: 0 0 2px deeppink, 0 0 5px black, inset 0 0 5px black;
      border-radius: 10px;
      background-color: #fff;
      flex-direction: column;
      box-sizing: border-box;
      text-align: center; }
      html .box .content h2, body .box .content h2 {
        color: deeppink; }
      html .box .content p, body .box .content p {
        color: dimgray; }

@keyframes animate {
  from {
    background-position: 0; }
  to {
    background-position: 10%; } }
